<template>
  <div class="FloatbtnBox" ref="FloatBox">
    <div class="rounded-circle Floatbtn BtnOne z-1" @click="OpenButton(Isshow)">
      <i class="bi bi-lightning-charge"></i>
    </div>
    <div class="rounded-circle Floatbtn BtnTwo z-0" @click="scrollToTop">
      <i class="bi bi-rocket"></i>
    </div>
    <div class="rounded-circle Floatbtn BtnTwo z-0" @click="GotoHome">
      <i class="bi bi-house"></i>
    </div>
    <div class="rounded-circle Floatbtn BtnTwo z-0" @click="ReturnLastPage">
      <i class="bi bi-reply"></i>
    </div>
  </div>
</template>

<script>
export default {
  name: "FloatButton",

  data() {
    return {
      Isshow: false,
    };
  },

  mounted() {},

  methods: {
    //打开按钮
    OpenButton(Isshow) {
      const buttonElement = this.$refs.FloatBox.childNodes;
      let moveNum = Isshow ? 0 : -60;
      for (let i = 0; i < buttonElement.length; i++) {
        if (i != 0) {
          buttonElement[i].style.transition = "0.5s";
          buttonElement[i].style.transform = `translateY(${moveNum * i}px)`;
        }
      }
      this.Isshow = !this.Isshow;
    },
    //回到顶部
    scrollToTop() {
      window.scrollTo({
        top: 0,
        behavior: "smooth",
      });
    },
    //回到首页
    GotoHome() {
      this.$router.push("/home");
    },
    ReturnLastPage() {
      this.$router.push("/List");
    },
  },
};
</script>

<style lang="scss" scoped>
.FloatbtnBox {
  transition: 0.3s;
  position: -webkit-sticky; /* For Safari */
  position: sticky;
  bottom: 150px; /* Required */
  margin-left: 20px;
  .BtnOne {
    background: #a2cbe6;
    box-shadow: inset 8px 8px 16px #469be0, inset -8px -8px 16px #86b3e7;
    color: rgb(248, 247, 250);
    &:hover {
      transition: 0.3s;
      box-shadow:  8px 8px 16px #d7e2e2,  -8px -8px 16px #f1f6fa;
    }
  }
  .BtnTwo {
    background: linear-gradient(145deg, #eff7fd, #c9d0d4);
    box-shadow: inset 8px 8px 16px #d3e1ec, inset -8px -8px 16px #7b8ea3;
    color: rgb(11, 86, 116);
    &:hover {
      transition: 0.3s;
      box-shadow:  8px 8px 16px #879c9cf6,  -8px -8px 16px #c9e0f0;
    }
  }
  .Floatbtn {
    position: absolute;
    width: 50px;
    height: 50px;
    background-color: beige;
    font-size: 2rem;
    text-align: center;
    line-height: 50px;
    margin-bottom: 10px;
    font-weight: 600;
  }
}
</style>
