import request from "@/utils/request";

//首页 专区数据
export  function NewZone(data = {}) {
  return  request({
    url: "/api/index/Zone",
    method: "get",
    params: data,
  });
}
//首页 文章数据
export  function NewArticle(data = {}) {
  return  request({
    url: "/api/index/Article",
    method: "get",
    params: data,
  });
}
 
