import request from "@/utils/request";

export function SwiperImg(data = {}) {
  return request({
    url: "/api/Article/List/Cover",
    method: "get",
    params: data,
  });
}

export function ArticleList(data = {}) {
  return request({
    url: "/api/Article/List/Article",
    method: "get",
    params: data,
  });
}

export function CategoryList(data = {}) {
  return request({
    url: "/api/Article/List/Category",
    method: "get",
    params: data,
  });
}
export function CollectionList(data = {}) {
  return request({
    url: "/api/Article/List/Collection",
    method: "get",
    params: data,
  });
}