<template>
  <!-- 知识专区 -->
  <div class="my-3 KnowledgeZoneBox rounded-5">
    <div class="dropdown w-100 d-flex justify-content-center">
      <button
        class="btn btn-secondary fw-bolder bg-transparent border-0 fs-3 Knowledgetitle dropdown-toggle"
        type="button"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        <i class="bi bi-book me-2"></i>
        <span>知识专区</span>
      </button>
      <ul class="dropdown-menu dropdown-menu-dark">
        <li>
          <a
            class="dropdown-item"
            :class="{ active: Isactive === index }"
            @click="Isactive = index"
            v-for="(downItem, index) in dataList"
            :key="downItem.id"
            href="#"
            >{{ downItem.ZoneName }}</a
          >
        </li>
      </ul>
    </div>
    <div class="swiper overflow-hidden" id="swiper_Knowledge">
      <div class="swiper-wrapper py-2">
        <div
          class="swiper-slide my-3 px-4 rounded-5"
          v-for="item in dataList"
          :key="item.id"
        >
          <div class="swiper_img">
            <img
              v-lazy="item.bgImg"
              :key="item.bgImg"
              class="w-100 h-100 opacity-50 object-cover rounded-5 d-block"
              alt="..."
            />
          </div>
          <div
            class="card-img-overlay text-light d-flex flex-column justify-content-end align-items-center"
          >
            <div class="text_title d-flex flex-column">
              <p class="text-center h1">
                <i class="bi bi-pc-display-horizontal"></i>
              </p>
              <p class="h3 fw-bolder">{{ item.ZoneName }}</p>
            </div>
            <div class="text-center w-75 pt-1 pb-2 Display">
              <small>{{ item.ZoneContext }}</small>
            </div>
            <div
              class="btn btn-primary Display"
              @click="goToArticleList(item.bgImg)"
            >
              进入专区
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "KnowledgeZone",
  data() {
    return {
      dataList: [],
      Isactive: 0,
    };
  },
  methods: {
    getData() {
      let CacheData = this.$GetStorage("IndexData");
      if (CacheData) {
        this.dataList = CacheData.ZoneData[0].KnowledgeZone;
        //等待数据遍历完成再初始化swiper
        this.$nextTick(() => {
          this.swiperInit();
        });
      }
    },
    swiperInit() {
      var mySwiper2 = new this.$swiper("#swiper_Knowledge", {
        direction: "horizontal", // 垂直切换选项
        slidesPerView: 1,

        grid: {
          fill: "row",
          rows: 1,
        },
        breakpoints: {
          100: {
            slidesPerView: 1,
            spaceBetween: 20,
          },

          768: {
            //当屏幕宽度大于等于768
            slidesPerView: 2,
            spaceBetween: 20,
          },
          980: {
            //当屏幕宽度大于等于768
            slidesPerView: 3,
            spaceBetween: 20,
          },
        },
      });
    },
    goToArticleList(img) {
      this.$store.commit("index/setBackground", img);
      this.$router.push("/list");
    },
  },

  mounted() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
$bg_color: rgba(48, 54, 105, 0.8);
.KnowledgeZoneBox {
  background-color: $bg_color;
  .Knowledgetitle {
    color: #2ca6c8;
  }
  .swiper-slide {
    transition: 0.3s;
    -webkit-transition: 0.3s;
    -moz-transition: 0.3s;
    -ms-transition: 0.3s;
    -o-transition: 0.3s;
    .text_title {
      font-family: "Noto Sans SC", sans-serif;
      transition: 0.3s;
      letter-spacing: 10px;
      -webkit-transition: 0.3s;
      -moz-transition: 0.3s;
      -ms-transition: 0.3s;
      -o-transition: 0.3s;
    }
    &:hover {
      transform: translateY(-20px);
      transition: 0.3s;
      -webkit-transform: translateY(-20px);
      -moz-transform: translateY(-20px);
      -ms-transform: translateY(-20px);
      -o-transform: translateY(-20px);
      img {
        box-shadow: 5px 10px 30px #2ca6c8, -5px -10px 30px #2ca6c8;
        opacity: 0.5;
        transition: 0.3s;
      }
      .text_title {
        transform: translate(0, -50px);
        -webkit-transform: translate(0, -50px);
        -moz-transform: translate(0, -50px);
        -ms-transform: translate(0, -50px);
        -o-transform: translate(0, -50px);
        transition: 0.3s;
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        color: #f2f5f7;
      }
      .Display {
        opacity: 1;
        transition: 0.3s;
        transform: translate(0, -50px);
        -webkit-transition: 0.3s;
        -moz-transition: 0.3s;
        -ms-transition: 0.3s;
        -o-transition: 0.3s;
        -webkit-transform: translate(0, -50px);
        -moz-transform: translate(0, -50px);
        -ms-transform: translate(0, -50px);
        -o-transform: translate(0, -50px);
      }
    }
    .Display {
      transition: 0.3s;
      opacity: 0;
      -webkit-transition: 0.3s;
      -moz-transition: 0.3s;
      -ms-transition: 0.3s;
      -o-transition: 0.3s;
    }
  }
}
</style>
