// initial state
// shape: [{ id, quantity }]
const state = () => ({
  defaultIMG: require("@/assets/ProjectImg/bg.png"),
  backgroundImage: require("@/assets/ProjectImg/bg.png"),
  OpenWinodow: false,
  ChangeType: 1,
  AticleParams: {
    CategoryID: null,
    PostTime: true,
    LiuLang: true,
    DianZhan: true,
  },
  ArticleContext: "",
  CommentList: [],
  Catalagues:[]
});

// getters
const getters = {};

// actions
const actions = {
  updatedCategory({ commit }, categoryId) {
    commit("setCategoryID", categoryId);
  },
  updatedsetFitterData({ commit }, FitterData) {
    commit("setFitterData", FitterData);
  },
  updatedsetArticleWithComments({ commit }, ArticleDataList) {
    commit("setArticleWithComments", ArticleDataList);
  },
};

// mutations
const mutations = {
  //根据专区封面修改列表页背景图片
  setBackground(state, newImage) {
    state.backgroundImage = newImage;
  },
  // 控制分类合集模块的显示与隐藏
  setChangeWindows(state, ChangeType) {
    state.OpenWinodow = ChangeType.Open;
    state.ChangeType = ChangeType.Type;
  },
  //修改文章列表获取分类ID
  setCategoryID(state, CategoryID) {
    state.AticleParams.CategoryID = CategoryID;
  },
  // 修改文章列表获取排序条件
  setFitterData(state, FitterData) {
    const { PostTime, LiuLang, DianZhan } = FitterData;
    state.AticleParams.PostTime = PostTime;
    state.AticleParams.LiuLang = LiuLang;
    state.AticleParams.DianZhan = DianZhan;
  },
  // 修改文章详情内容和评论数据
  setArticleWithComments(state, ArticleDataList) {
    state.ArticleContext = ArticleDataList.ArticleContexts;
    state.CommentList = ArticleDataList.Comments;
  },
  // 修改目录列表数据
  setCatalaguesData(state,CatalogsData) {
    state.Catalagues=CatalogsData;
  }
};

export default {
  namespaced: true,
  state,
  getters,
  actions,
  mutations,
};
