<template>
  <!-- 博客动态 -->
  <div class="dynamic border-1 my-3 rounded-4 p-3">
    <div
      class="btn-group d-flex justify-content-center"
      role="group"
      aria-label="Basic example"
    >
      <button
        type="button"
        class="btn btn-primary border border-light-subtle bg-transparent"
        @click="switchArticle(true)"
      >
        <i class="bi bi-calendar-check text-info-emphasis"></i>最新发布文章
      </button>
      <button
        type="button"
        class="btn btn-primary border border-light-subtle bg-transparent"
        @click="switchArticle(false)"
      >
        <i class="bi bi-fire text-danger"></i>最热门文章
      </button>
    </div>
    <div class="swiper py-3 overflow-hidden" id="swiper_dynamic">
      <div class="swiper-wrapper">
        <div
          class="swiper-slide card rounded-3"
          v-for="item in Article"
          :key="item.id"
        >
          <img
            v-lazy="item.bgImg"
            :key="item.bgImg"
            class="card-img-top img-thumbnail img-fluid object-fit-cover"
            alt="..."
            style="height: 200px"
          />
          <span class="card-body">
            <h5 class="card-title fw-bold font-monospace">
              {{ item.title }}
            </h5>
            <p class="card-text">
              {{ item.context }}
            </p>
            <p class="card-text">
              <small class="text-muted d-flex justify-content-between">
                <span class="text-light">
                  <i class="bi bi-hand-thumbs-up px-1"> {{ item.like }} </i>
                  <i class="bi bi-eye px-1"> {{ item.Traffic }} </i>
                  <i class="bi bi-chat-right-heart px-1">
                    {{ item.comment }}
                  </i>
                </span>
                <span class="text-light">
                  <i class="bi bi-calendar-check px-1"> {{ item.date }} </i>
                </span>
              </small>
            </p>
          </span>
        </div>
      </div>
      <div class="swiper-pagination"></div>
      <!--分页器。如果放置在swiper外面，需要自定义样式。-->
    </div>
  </div>
</template>

<script>
import { NewArticle } from "@/api";
export default {
  name: "dynamic",
  data() {
    return {
      Article: [],
      HotArticle: [],
      NewArticle: [],
    };
  },
  methods: {
    getData() {
      NewArticle()
        .then((res) => {
          if (res.status === 200) {
            this.HotArticle = res.HotArticle;
            this.NewArticle = res.NewArticle;
            this.Article = this.NewArticle;
            //等待数据遍历完成再初始化swiper
            this.$nextTick(() => {
              this.swiperInit();
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    swiperInit() {
      this.mySwiper = new this.$swiper("#swiper_dynamic", {
        pagination: {
          el: ".swiper-pagination",
        },
        loop: true,
        effect: "coverflow",
        slidesPerView: 1,
        centeredSlides: true,
        breakpoints: {
          100: {
            slidesPerView: 1,
            spaceBetween: 20,
          },

          768: {
            //当屏幕宽度大于等于768
            slidesPerView: 2,
            spaceBetween: 20,
          },
          980: {
            //当屏幕宽度大于等于768
            slidesPerView: 3,
            spaceBetween: 20,
          },
        },
        coverflowEffect: {
          rotate: 20,
          stretch: 10,
          depth: 150,
          modifier: 1,
          slideShadows: true,
        },
      });
    },
    switchArticle(num) {
      if (num) {
        this.Article = this.NewArticle;
      } else {
        this.Article = this.HotArticle;
      }
      this.mySwiper.destroy(true, true);
      this.$nextTick(() => {
        this.swiperInit();
      });
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
$bg_color: rgba(48, 54, 105, 0.8);
.dynamic {
  background-color: $bg_color;
  .btn:hover {
    box-shadow: 5px 10px 30px #2ca6c8, -5px -10px 30px #2ca6c8;
  }

  .card {
    background-color: $bg_color;
    .card-title {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 2;
      overflow: hidden;
      color: #f7faf4;
    }
    .card-text {
      display: -webkit-box;
      -webkit-box-orient: vertical;
      -webkit-line-clamp: 3;
      overflow: hidden;
      color: #d5e5ed;
    }
  }
  .card:hover {
    box-shadow: 5px 10px 30px #2ca6c8, -5px -10px 30px #2ca6c8;
  }
}
</style>
