<template>
  <div class="p-2 ArticleContextBox border border-1">
    <div class="d-flex justify-content-between text-light p-2 fs-6">
      <div class="w-50 pe-3 text-start d-flex">
        <i class="bi bi-chevron-double-left"></i
        ><span style="width: 100px">上一篇:</span>
        <span class="text-info text-truncate d-block"
          >点击按键送达时间搭建了点击按键送达时间搭建了</span
        >
      </div>
      <div class="w-50 text-end ps-3 d-flex">
        <span class="text-info text-truncate d-block text-end"
          >点击按键送达时间击按键送达时间搭建了</span
        ><span style="width: 70px">:下一篇</span
        ><i class="bi bi-chevron-double-right"></i>
      </div>
    </div>
    <v-md-preview
      :text="ArticleContext"
      ref="preview"
      class="ArticleContext bg-light"
    >
    </v-md-preview>
  </div>
</template>

<script>
export default {
  name: "ArticleContext",
  data() {
    return {};
  },
  updated() {
    this.$nextTick().then(() => {
      this.GetArticleCatalogue();
    });
  },
  computed: {
    ArticleContext() {
      return this.$store.state.index.ArticleContext;
    },
  },
  methods: {
    GetArticleCatalogue() {
      const anchors =
        this.$refs.preview.$el.querySelectorAll("h1,h2,h3,h4,h5,h6");
      const titles = Array.from(anchors).filter(
        (title) => !!title.innerText.trim()
      );
      let Catalagues = [];
      if (!titles.length) {
        Catalagues = [];
        return;
      }

      const hTags = Array.from(
        new Set(titles.map((title) => title.tagName))
      ).sort();

      Catalagues = titles.map((el) => ({
        title: el.innerText,
        lineIndex: el.getAttribute("data-v-md-line"),
        indent: hTags.indexOf(el.tagName),
      }));
      this.$store.commit("index/setCatalaguesData", Catalagues);
    },
  },
};
</script>

<style lang="scss" scoped>
.ArticleContextBox {
  background-color: rgba(48, 54, 105, 0.8);
  .ArticleContext {
    min-height: 1000px;
  }
}
</style>
