<template>
  <div
    class="mt-3 mb-2   pb-2 border border-1 rounded-4 LevelBox d-flex align-items-center flex-column position-relative "
  >
    <div
      class="position-absolute rounded-circle d-lg-none d-block text-center CloseBtn"
      @click="Close()"
    >
      <i class="bi bi-x-octagon"></i>
    </div>
    <div class="text-center mb-2 h3  w-100 fw-bolder my-2 position-sticky text-white"><i class="bi bi-list"></i>分类</div>
    <div
      v-for="(item, index) in Categories"
      :key="item.id"
      class="mb-2 px-2 w-100 h-100"
    >
      <div class="TopLevel  rounded-2 py-2">
        <div
          class="d-flex text-center justify-content-center align-items-center"
          @click="ClickSecondShow(item.id)"
          @dblclick="dblclickCategory(item.id)"
        >
          {{ item.Name }}
        </div>
        <div class="row g-0" v-show="ClickIndexList1.includes(item.id)">
          <div
            class="p-1 col-4"
            v-for="Subitem in item.subcategories"
            :key="Subitem.id"
          >
            <div class="border-1 border rounded-2 SecondLevel">
              <div
                class="d-flex p-1 text-center justify-content-center align-items-center h-25 SecondLevelTitle"
                @click="ClickThirdShow(`${index}_${Subitem.id}`)"
                @dblclick="dblclickCategory(Subitem.id)"
              >
                {{ Subitem.Name }}
              </div>
              <div
                class="w-100 h-100 p-1 h-75"
                v-show="ClickIndexList2.includes(`${index}_${Subitem.id}`)"
              >
                <div
                  class="border-1 border p-1 mb-1 1h-1 rounded-2 ThirdLevel"
                  v-for="SubitemChild in Subitem.subcategories"
                  :key="SubitemChild.id"
                  @dblclick="dblclickCategory(SubitemChild.id)"
                >
                  <div
                    class="d-flex text-center justify-content-center align-items-center"
                  >
                    {{ SubitemChild.Name }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { CategoryList } from "@/api/List";
import { SetStorage, GetStorage } from "@/utils/LocalStorage";
export default {
  name: "ClassIfication",
  data() {
    return {
      Categories: [],
      ClickIndexList1: [],
      ClickIndexList2: [],
    };
  },

  mounted() {
    //获取缓存数据，没有则请求接口获取并缓存
    let categoryCache = GetStorage("CategoryData");
    if (categoryCache) {
      this.Categories = categoryCache;
    } else {
      this.GetCategoryData();
    }
  },

  methods: {
    //获取分类数据
    GetCategoryData() {
      CategoryList()
        .then((res) => {
          if (res.status == 200) {
            this.Categories = res.CategoryList;
            SetStorage("CategoryData", res.CategoryList, 24 * 60 * 60 * 30);
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    ClickThirdShow(ID) {
      let HasIndex = this.ClickIndexList2.indexOf(ID);
      if (HasIndex == -1) {
        this.ClickIndexList2.push(ID);
      } else {
        this.ClickIndexList2.splice(HasIndex, 1);
      }
    },
    ClickSecondShow(index) {
      let HasIndex = this.ClickIndexList1.indexOf(index);
      if (HasIndex == -1) {
        this.ClickIndexList1.push(index);
      } else {
        this.ClickIndexList1.splice(HasIndex, 1);
        this.ClickIndexList2.forEach((CID) => {
          if (CID.indexOf(index + "_")) {
            this.ClickIndexList2.pop(CID);
          }
        });
      }
    },
    Close() {
      this.$store.commit("index/setChangeWindows", { Open: false, Type: 1 });
    },
    dblclickCategory(categoryId) {
      this.$store.dispatch("index/updatedCategory",categoryId)
      this.Close();
    },
  },
  computed: {},
};
</script>

<style lang="scss" scoped>
.LevelBox {
  background-color: rgba(48, 54, 105, 0.8);
  max-height: 600px;
  min-height: 220px;
  display: flex;
  flex-direction: column;
  overflow-y: auto; /* 显示滚动条，仅在内容溢出时显示 */
  scrollbar-width: thin; /* 设置滚动条宽度 */
  scrollbar-color: rgb(123, 192, 238) rgb(123, 192, 238); /* 设置滚动条颜色 */
  .CloseBtn {
    right: 0;
    top: -3px;
  }
  .TopLevel {
    font-size: 0.9rem;
    background-color: #FFFFFF;
    .SecondLevel {
      font-size: 0.75rem;
      min-width: 40px;
      background-color: #99cccc;
      .SecondLevelTitle {
        min-height: 40px;
      }
      .ThirdLevel {
        background-color: #bfd5eb;
      }
    }
  }
}
.LevelBox::-webkit-scrollbar {
  width: 0.2em; /* 设置滚动条宽度 */
}

.LevelBox::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(123, 192, 238); /* 设置滚动条滑块颜色 */
}
</style>
