<template>
  <div
    class="rounded-top-2 mt-1 py-1 d-flex justify-content-around bg-light-subtle row g-0"
  >
    <div class="col-2 d-lg-none text-center" @click="OpenCategory(1)"><i class="bi bi-list">分类</i></div>
    <div class="col-8 co-lg-12 d-flex justify-content-around">
      <div @click="ChageIcons('PostTime')">
        <i class="bi text-info" :class="ChageIcon.PostTime"></i>发布时间
      </div>
      <div @click="ChageIcons('LiuLang')">
        <i class="bi text-info" :class="ChageIcon.LiuLang"></i>浏览量
      </div>
      <div @click="ChageIcons('DianZhan')">
        <i class="bi text-info" :class="ChageIcon.DianZhan"></i>点赞量
      </div>
    </div>
    <div class="col-2 d-lg-none text-center" @click="OpenCategory(0)"><i class="bi bi-folder">合集</i></div>
  </div>
</template>

<script>
export default {
  name: "FilterBar",

  data() {
    return {
      IconList: ["bi-sort-up", "bi-sort-up-alt"],
      ChageIcon: {
        PostTime: "bi-sort-up",
        LiuLang: "bi-sort-up",
        DianZhan: "bi-sort-up",
      },
    };
  },

  mounted() {},

  methods: {
    OpenCategory(type) {
      this.$store.commit("index/setChangeWindows", { Open: true, Type: type });
    },
    ChageIcons(num) {
      let Icons = this.IconList;
      let Chage = this.ChageIcon;
      const Data = this.$store.state.index.AticleParams;
      Chage[num] == Icons[0]
        ? (this.ChageIcon[num] = Icons[1])
        : (this.ChageIcon[num] = Icons[0]);
      Data[num] = !Data[num];
      this.$store.dispatch("index/updatedsetFitterData", Data);
    },
  },
};
</script>

<style lang="scss" scoped></style>
