<template>
  <div class="ArticleInfo border border-1 text-white position-relative">
    <img
      v-lazy="ArticleContextData.cover"
      :key="ArticleContextData.cover"
      class="w-100 h-100 object-fit-cover"
    />
    <div
      class="position-absolute top-50 w-100 translate-middle start-50 d-flex flex-column align-items-center justify-content-center"
    >
      <div class="Title fs-1 fw-bold lh-1 text-center">{{ ArticleContextData.Title }}</div>
      <div class="Author fs-4 border p-1 rounded-1 my-2">
        <i class="bi bi-person">{{ ArticleContextData.AuthorName }}</i>
      </div>
      <div class="d-lg-flex my-2 justify-content-center">
        <div class="d-flex">
          <div class="mx-3">
            <i class="bi bi-hand-index-thumb"> {{ ArticleContextData.Like }}</i>
          </div>
          <div class="mx-3">
            <i class="bi bi-eye"> {{ ArticleContextData.Browser }}</i>
          </div>
          <div class="mx-3">
            <i class="bi bi-chat-right-dots">
              {{ ArticleContextData.CommentNum }}</i
            >
          </div>
        </div>
        <div class="mx-3 text-center">
          <i class="bi bi-calendar-event"></i>
          {{ ArticleContextData.date }}
        </div>
      </div>
      <div class="">
        <span
          class="border border-1 rounded-3 px-1 mx-1 Label"
          v-for="(Label, index) in ArticleContextData.label"
          :key="index + Label"
          ><small>#{{ Label }}</small>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
import { ArticleContext } from "@/api/Details";
export default {
  name: "CoverContext",
  data() {
    return {
      ArticleContextData: {},
    };
  },

  mounted() {
    this.GetArticleContextData();
  },

  methods: {
    async GetArticleContextData() {
      await ArticleContext()
        .then((res) => {
          if (res.status == 200) {
            this.ArticleContextData = res.ArticleContext;
            this.$store.dispatch("index/updatedsetArticleWithComments", {
              ArticleContexts: res.ArticleContext.context,
              Comments: res.ArticleContext.Comment,
            });
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>

<style lang="scss" scoped>
.ArticleInfo {
  height: 300px;
  .Author {
    background: rgba(255, 255, 255, 0.4);
    color: rgb(221, 230, 236);
  }
  .Label {
    background: rgba(100, 229, 121, 0.7);
  }
}
</style>
