<template>
  <!-- 博客介绍 -->
  <div class="card ThemWallBox border p-2" style="max-width: 100%">
    <div class="row g-0">
      <div class="col-lg-8 col-md-6">
        <img
          v-lazy="ThemWallImg"
          :key="ThemWallImg"
          class="img-fluid rounded-4"
          alt="..."
        />
      </div>
      <div
        id="ThemWallBox_Text"
        class="card-body col-lg-4 col-md-6 d-flex flex-xl-column"
      >
        <img
          src="@/assets/image/星球011.svg"
          class="w-100 h-75 img-fluid d-none d-xl-block"
          alt=""
          srcset=""
        />
        <div class="text-center">
          {{ ThemWallContext }}
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Themwall",
  data() {
    return {
      ThemWallImg: "",
      ThemWallContext: "",
    };
  },
  methods: {
    getData() {
      let CacheData = this.$GetStorage("IndexData");
      if (CacheData) {
        this.ThemWallImg = CacheData.ZoneData[0].ThemWall.ThemWallImg;
        this.ThemWallContext = CacheData.ZoneData[0].ThemWall.ThemWallContext;
      }
    },
  },
  computed: {},
  mounted() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
$bg_color: rgba(48, 54, 105, 0.8);
.ThemWallBox {
  border-radius: 21px;
  background: $bg_color;
  #ThemWallBox_Text {
    background-color: $bg_color;
    box-shadow: -100px -20px 90px rgb(48, 54, 105);
    height: 200px;
    color: #daddeb;
  }
}
.ThemWallBox:hover {
  box-shadow: 5px 10px 30px #2ca6c8, -5px -10px 30px #2ca6c8;
}
</style>
