<template>
  <!-- 项目专区 -->
  <div class="my-3 KnowledgeZoneBox rounded-5">
    <div class="dropdown w-100 d-flex justify-content-center">
      <button
        class="btn btn-secondary fw-bolder bg-transparent border-0 fs-3 Knowledgetitle dropdown-toggle"
        type="button"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        <i class="bi bi-book me-2"></i>
        <span>项目专区</span>
      </button>
      <ul class="dropdown-menu dropdown-menu-dark">
        <li>
          <a
            class="dropdown-item"
            :class="{ active: Isactive === index }"
            @click="Isactive = index"
            v-for="(downItem, index) in dataList"
            :key="downItem.id"
            href="#"
            >{{ downItem.ZoneName }}</a
          >
        </li>
      </ul>
    </div>
    <div class="row g-0">
      <div
        class="col-md-3 col-4 px-lg-5 px-2 py-2"
        v-for="item in dataList"
        :key="item.id"
      >
        <div
          class="text-center text-light project  rounded-3 d-flex flex-column justify-content-center align-items-center"
        >
          <div class="imgBox py-2">
            <img
              class="img-fluid w-100 h-100"
              :src="item.bgImg"
              alt=".."
              srcset=""
            />
          </div>
          <div class="py-2 fs-6 fw-medium title">{{ item.ZoneName }}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "project",
  data() {
    return {
      dataList: [],
      Isactive: 0,
    };
  },
  methods: {
    getData() {
      let CacheData = this.$GetStorage("IndexData");
      if (CacheData) {
        this.dataList = CacheData.ZoneData[0].ProjectZone;
      }
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
$bg_color: rgba(48, 54, 105, 0.8);
/* 项目专区 */
.KnowledgeZoneBox {
  background-color: $bg_color;
  .Knowledgetitle {
    color: #2ca6c8;
  }
  .project {
    height: 130px;
    background-color: $bg_color;
    border: 1px solid #0aa7db;
    .imgBox {
      height: 60%;
      width: 60%;
    }
    &:hover {
      box-shadow: 5px 10px 30px #2ca6c8, -5px -10px 30px #2ca6c8;
      text-shadow: 0px -5px 5px #dae4e8;
      transform: perspective(100px) translate3d(0px, 10px, 10px);
      -webkit-transform: perspective(100px) translate3d(0px, 10px, 10px);
      -moz-transform: perspective(100px) translate3d(0px, 10px, 10px);
      -ms-transform: perspective(100px) translate3d(0px, 10px, 10px);
      -o-transform: perspective(100px) translate3d(0px, 10px, 10px);
    }

  }
}
</style>
