<template>
  <div class="" id="TopA" >
    <div class="container-lg" @click="OutIsShow=false">
      <CoverContextVue />
      <div class="row g-0 my-3 d-flex justify-content-between">
        <CatalogueVue
          id="Catalogue"
          @HeadingClicked="ClickCatalogue"
          class="col-lg-2 d-none d-lg-block Cataloguefixed"
        />
        <ArticleContextVue ref="ArticleContext" class="col-lg-7 col-12" />
        <div class="col-lg-3 d-none d-lg-block">
          <SimilarArticlesVue />
          <CommontVue />
        </div>
      </div>
    </div>
    <FloatButtonVue />
    <!--手机版目录-->
    <transition name="fade">
      <div class="sticky-bottom bg-black w-100" v-show="OutIsShow">
        <CatalogueVue
          @HeadingClicked="ClickCatalogue"
          class="col-lg-2 Cataloguefixed h-100"
          v-show="CatalogueIsShow"
        />
        <CommontVue v-show="CommontIsShow" />
      </div>
    </transition>
    <div class="d-block d-lg-none sticky-bottom bg-light-subtle p-2 d-flex justify-content-around mt-2">
      <span @click="CatalogueShow(!CatalogueIsShow)"
        ><i class="bi bi-list-stars"></i>目录</span
      >
      <span @click="CommentShow(!CommontIsShow)"
        ><i class="bi bi-chat-dots"></i>评论</span
      >
      <span><i class="bi bi-hand-thumbs-up"></i>点赞</span>
      <span><i class="bi bi-star"></i>收藏</span>
    </div>
  </div>
</template>

<script>
import FloatButtonVue from "@/components/header/FloatButton.vue"
import CatalogueVue from "@/components/Detailes/Catalogue.vue";
import SimilarArticlesVue from "@/components/Detailes/SimilarArticles";
import ArticleContextVue from "@/components/Detailes/ArticleContext.vue";
import CommontVue from "@/components/Detailes/Commont.vue";
import CoverContextVue from "@/components/Detailes/CoverContext.vue";
import _ from "lodash";
export default {
  name: "ArticleDetails",

  data() {
    return {
      scrollPosition: 0,
      elementTopOffset: 10,
      OutIsShow: false,
      CommontIsShow: false,
      CatalogueIsShow: false,
     
    };
  },
  components: {
    CatalogueVue,
    SimilarArticlesVue,
    ArticleContextVue,
    CommontVue,
    CoverContextVue,
    FloatButtonVue,
  },
  computed: {},
  methods: {
    ClickCatalogue(anchor) {
      const { preview } = this.$refs.ArticleContext.$refs;
      const { lineIndex } = anchor;

      const heading = preview.$el.querySelector(
        `[data-v-md-line="${lineIndex}"]`
      );

      if (heading) {
        // 注意：如果你使用的是编辑组件的预览模式,则这里的方法名改为 previewScrollToTarget
        preview.scrollToTarget({
          target: heading,
          scrollContainer: window,
          top: 100,
        });
      }
    },
    CatalogueShow(lineShow) {
      this.OutIsShow=true;
      this.CommontIsShow=false;
      this.CatalogueIsShow = lineShow;  

    },
    CommentShow(lineShow) {
      this.OutIsShow=true;
      this.CommontIsShow=lineShow;
      this.CatalogueIsShow =false;   
    },
  },
};
</script>

<style lang="scss" scoped>
.Cataloguefixed {
  transition: 0.3s;
  position: -webkit-sticky; /* For Safari */
  position: sticky;
  top: 85px; /* Required */
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s;
}

.fade-enter,
.fade-leave-to {
  opacity: 0;
}
</style>
