/**
 * 封装缓存功能，增加过期时间
 * @param {string} StorageName  缓存名字
 * @param {object} data         缓存数据
 * @param {number} expiresAt    过期时间
 */
export function SetStorage(StorageName, data, expiresAt=24*60*60) {
  // 存储数据并添加过期时间
  const Setdata = {
    Data: data,
    expiresAt:Date.now()+expiresAt, // 过期时间
  };
  localStorage.setItem(StorageName, JSON.stringify(Setdata));
}

/**
 * 缓存功能，检查是否过期
 * @param {string} StorageName  缓存名字
 */
export function GetStorage(StorageName) {
  // 读取数据并检查过期时间
  const storedData = localStorage.getItem(StorageName);
  if (storedData) {
    const parsedData = JSON.parse(storedData);
    if (parsedData.expiresAt > Date.now()) {
      // 数据未过期，可以使用
      return parsedData.Data
    } else {
      // 数据已过期，需要删除
      localStorage.removeItem(StorageName);
      return null
    }
  }
}
