<template>
  <div class=" CatalogueBox  rounded-1 ">
    <div class="text-center h3 text-light py-1 sticky-top border border-1">目录</div>
    <ul class="px-1 bg-light " style="min-height: 600px; ">
      <li
        v-for="(item, index) in Catalogues"
        :key="index"
        :style="{ padding: `0px 0 5px ${item.indent * 10}px`,fontSize:`${1.2-(item.indent/10)}rem` }"
        @click="handleAnchorClick(item)"
        class=""
      >
        <a class="CatalogueTitle  text-decoration-none">{{ item.title }}</a>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: "Catalogue",

  data() {
    return {};
  },
  computed: {
    Catalogues() {
      return this.$store.state.index.Catalagues;
    },
  },
  mounted() {},

  methods: {
    handleAnchorClick(anchor) {
      this.$emit("HeadingClicked", anchor); // 触发自定义事件并传递位置参数
    },
  },
};
</script>

<style lang="scss" scoped>
.CatalogueBox {
  cursor: pointer;
  background-color: rgba(48, 54, 105, 0.8);
  .CatalogueTitle {
    color: rgb(96, 139, 139);
    &:hover {
      color: rgb(21, 243, 169);
    }

  }
}
</style>
