<template>
  <div
    class="CollectionBox mt-3 mb-2 pb-2 bg-light-subtle rounded-3 d-flex align-items-center flex-column position-relative"
  >
    <div
      class="position-absolute rounded-circle d-lg-none d-block text-center CloseBtn"
      @click="Close()"
    >
      <i class="bi bi-x-octagon"></i>
    </div>
    <div class="bg-light-subtle w-100 text-center my-2 h3 fw-bolder  CloseBtn position-sticky"><i class="bi bi-folder"></i> 合集 </div>
    <div
      v-for="item in CollectionList"
      :key="item.id"
      class="rounded-3 border border-1 m-1 p-1"
    >
      <div class="Title mb-1 h6 fw-bolder w-100 text-center">{{ item.Title }}</div>
      <div class="d-flex">
        <div class="w-25 h-100 me-1">
          <img
            v-lazy="item.bgImg"
            :key="item.bgImg"
            alt="..."
            class="img-fluid object-fit-cover w-100 h-100"
          />
        </div>
        <div class="w-75">
          <div class="context lh-sm ">{{ item.context }}</div>
        </div> 
      </div>
      <small class="d-flex justify-content-around my-2">
            <div class="date">{{ item.date }}</div>
            <div class="Count">
              文章总数：<em class="text-danger">{{ item.Count }}</em>
            </div>
          </small>
    </div>
  </div>
</template>

<script>
import { CollectionList } from "@/api/List";
export default {
  name: "Collection",

  data() {
    return {
      CollectionList: [],
    };
  },

  mounted() {
    CollectionList()
      .then((res) => {
        console.log(res);
        if (res.status == 200) {
          this.CollectionList = res.CollectionList;
        }
      })
      .catch((err) => {
        console.log(err);
      });
  },

  methods: {
    Close() {
      this.$store.commit("index/setChangeWindows", { Open: false, Type: 1 });
    },
  },
};
</script>

<style lang="scss" scoped>
.CollectionBox {
  max-height: 400px;
  overflow-y: auto;
  scrollbar-width: thin; /* 设置滚动条宽度 */
  scrollbar-color: rgb(123, 192, 238) rgb(123, 192, 238); /* 设置滚动条颜色 */
  .CloseBtn {
    right: 0;
    top: -3px;
  }

  .context {
    font-size: 0.7rem;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    overflow: hidden;
  }
}
.CollectionBox::-webkit-scrollbar {
  width: 0.2em; /* 设置滚动条宽度 */
}

.CollectionBox::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: rgb(123, 192, 238); /* 设置滚动条滑块颜色 */
}
</style>
