import vue from "vue";
import VueRouter from "vue-router";
import { SetStorage, GetStorage } from "@/utils/LocalStorage";
import { NewZone } from "@/api";

vue.use(VueRouter);
import Home from "@/view/Index";
import Login from "@/view/Login";
import Search from "@/view/Search";
import List from "@/view/ArticleList";
import Details from "@/view/ArticleDetails";
const meta = {
  Show: true,
};

const router = new VueRouter({
  routes: [
    {
      path: "/home",
      component: Home,
      meta,
    },
    {
      name: "search",
      path: "/search/:kw",
      component: Search,
      meta: { Show: false },
    },
    {
      path: "/login",
      component: Login,
      meta: { Show: false },
    },
    {
      path: "/list",
      component: List,
      meta,
    },
    {
      path: "/details",
      component: Details,
      meta,
    },
    //重定向路由
    {
      path: "/",
      redirect: "/home",
    },
  ],
});

//前置路由守卫
router.beforeEach((to, from, next) => {
  if (to.path == "/home") {
    IsCacheIndex(next);
  } else {
    next();
  }
});
router.afterEach((to, from) => {});

//首页判断是否有缓存
async function IsCacheIndex(next) {
  const CacheData = GetStorage("IndexData");
  if (!CacheData) {
    await NewZone()
      .then((res) => {
        if (res.status === 200) {
          SetStorage("IndexData", res, 72 * 60 * 60 * 1000); //三天后过期
          next();
        }
      })
      .catch((err) => {
        console.log(err);
      });
  } else {
    next();
  }
}

//重写push/replace
const originPush = VueRouter.prototype.push;
VueRouter.prototype.push = function (location, resolve, reject) {
  if (resolve && reject) {
    originPush.call(this, location, resolve, reject);
  } else {
    originPush.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};
const originReplace = VueRouter.prototype.replace;
VueRouter.prototype.push = function (location, resolve, reject) {
  if (resolve && reject) {
    originReplace.call(this, location, resolve, reject);
  } else {
    originReplace.call(
      this,
      location,
      () => {},
      () => {}
    );
  }
};

export default router;
