<template>
  <!-- 分享专区 -->
  <div class="my-3 KnowledgeZoneBox rounded-5">
    <div class="dropdown w-100 d-flex justify-content-center">
      <button
        class="btn btn-secondary fw-bolder bg-transparent border-0 fs-3 Knowledgetitle dropdown-toggle"
        type="button"
        data-bs-toggle="dropdown"
        aria-expanded="false"
      >
        <i class="bi bi-book me-2"></i>
        <span>分享专区</span>
      </button>
      <ul class="dropdown-menu dropdown-menu-dark">
        <li>
          <a
            class="dropdown-item"
            :class="{ active: Isactive === index }"
            @click="Isactive = index"
            v-for="(downItem, index) in dataList"
            :key="downItem.id"
            href="#"
            >{{ downItem.ZoneName }}</a
          >
        </li>
      </ul>
    </div>
    <div class="row g-0">
      <div class="col-6 col-lg-4 p-3" v-for="item in dataList" :key="item.id">
        <div
          class="share d-flex justify-content-start align-items-center rounded-3"
        >
          <div class="imgBox m-3">
            <img
              :src="item.bgImg"
              class="h-100 w-100 img-fluid"
              alt=""
              srcset=""
            />
          </div>
          <p class="text-light fw-bolder fs-4 p-2">{{ item.ZoneName }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "share",
  data() {
    return {
      dataList: [],
      Isactive: 0,
    };
  },
  methods: {
    getData() {
      let CacheData = this.$GetStorage("IndexData");
      if (CacheData) {
        this.dataList = CacheData.ZoneData[0].ShareZone;
      }
    },
  },
  mounted() {
    this.getData();
  },
};
</script>

<style lang="scss" scoped>
$bg_color: rgba(48, 54, 105, 0.8);

/* 分享专区 */
.KnowledgeZoneBox {
  background-color: $bg_color;
  .Knowledgetitle {
    color: #2ca6c8;
  }
  .share {
    background-color: $bg_color;
    box-shadow: 0px 5px 20px #1f9de0, 0px -5px 20px #1f9de0;
    .imgBox {
      height: 50px;
      width: 50px;
    }
    &:hover {
      background-color: $bg_color;
      box-shadow: 0px 5px 20px #f4f4f4, 0px -5px 20px #ebebec;
    }
  }
}
</style>
