<template>
  <div
    class="d-flex justify-content-between flex-column AritcleBox"
    @click="GotoDetails()"
  >
    <div class="row g-0">
      <div
        class="articleList px-2 pt-3 col-lg-4 col-sm-6 col-12"
        v-for="item in ArticleList"
        :key="item.id"
      >
        <div
          class="articleBox w-100 h-100 rounded-bottom-3 rounded-top-2 overflow-hidden d-flex flex-column justify-content-between"
        >
          <img
            v-lazy="item.bgImg"
            :key="item.bgImg"
            class="articleCover object-fit-cover w-100"
          />
          <div class="articleText lh-sm p-2">
            <div class="articleTitle">{{ item.title }}</div>
          </div>
          <div class="mt-2">
            <span
              class="border border-1 rounded-3 px-1 mx-1 Label"
              v-for="(Label, index) in item.label"
              :key="index + Label"
              ><small>#{{ Label }}</small></span
            >
          </div>
          <div class="articleLabel">
            <small class="text-muted d-flex justify-content-around">
              <i class="bi bi-hand-thumbs-up">{{ item.like }}</i>
              <i class="bi bi-eye">{{ item.Traffic }}</i>
              <i class="bi bi-chat-right-heart">{{ item.comment }}</i>
              <i class="bi bi-calendar-check">{{ item.date }}</i>
            </small>
          </div>
        </div>
      </div>
    </div>
    <nav aria-label="Page navigation example " class="mt-3">
      <ul class="pagination justify-content-center">
        <li class="page-item">
          <a class="page-link" href="#" aria-label="Previous">
            <span aria-hidden="true">&laquo;</span>
          </a>
        </li>
        <li class="page-item"><a class="page-link" href="#">1</a></li>
        <li class="page-item">
          <a class="page-link" href="#" aria-label="Next">
            <span aria-hidden="true">&raquo;</span>
          </a>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
import { ArticleList } from "@/api/List";
export default {
  name: "Article",

  data() {
    return {
      ArticleList: [],
    };
  },

  mounted() {
    this.GetArticleList();
  },
  computed: {
    GetCategoryID() {
      return this.$store.state.index.AticleParams;
    },
  },
  watch: {
    GetCategoryID: {
      handler(newVal, oldVal) {
        // 监听对象数据的变化并执行相应的处理逻辑
        this.RefreshArticles();
      },
      deep: true,
    },
  },
  methods: {
    RefreshArticles() {
      this.GetArticleList();
    },
    GetArticleList() {
      ArticleList(this.$store.state.index.AticleParams)
        .then((res) => {
          if (res.status == 200) {
            this.ArticleList = res.ArticleList;
          }
        })
        .catch((err) => {
          console.log(err);
        });
    },
    GotoDetails() {
      this.$router.push("/details");
    },
  },
};
</script>

<style lang="scss" scoped>
.AritcleBox {
  .articleList {
    min-height: 250px;
    .Label {
      font-size: x-small;
      white-space: nowrap;
      display: inline-block;
      overflow: hidden;
      text-overflow: ellipsis;
      background: rgb(233, 225, 225);
    }
    .articleBox {
      border-radius: 22px;
      background: rgb(246, 243, 249);
      transition: 0.3s linear;
      -webkit-transition: 0.3s linear;
      -moz-transition: 0.3s linear;
      -ms-transition: 0.3s linear;
      -o-transition: 0.3s linear;
      .articleCover {
        height: 60%;
      }
      .articleText {
        color: rgb(30, 54, 109);
        height: 30%;
        transition: 0.3s linear;
        -webkit-transition: 0.3s linear;
        -moz-transition: 0.3s linear;
        -ms-transition: 0.3s linear;
        -o-transition: 0.3s linear;
      }
      .articleLabel {
        height: 10%;
      }
    }
  }
  .articleList:hover {
    padding: 0.5rem 0.5rem 0 0.5rem;

    .articleBox {
      transition: 0.3s linear;
      -webkit-transition: 0.3s linear;
      -moz-transition: 0.3s linear;
      -ms-transition: 0.3s linear;
      -o-transition: 0.3s linear;
      transform: perspective(100px) translate3d(0px, 3px, 3px);
      box-shadow: 0px 20px 30px rgb(102, 99, 99);
      -webkit-transform: perspective(100px) translate3d(0px, 3px, 3px);
      -moz-transform: perspective(100px) translate3d(0px, 3px, 3px);
      -ms-transform: perspective(100px) translate3d(0px, 3px, 3px);
      -o-transform: perspective(100px) translate3d(0px, 3px, 3px);
    }
    .articleText {
      transition: 0.3s linear;
      -webkit-transition: 0.3s linear;
      -moz-transition: 0.3s linear;
      -ms-transition: 0.3s linear;
      -o-transition: 0.3s linear;
    }
  }
}
</style>
