import Vue from "vue";
import App from "./App.vue";
import "./Mock/index.js";

import store from './store'
import "bootstrap/dist/css/bootstrap.css"; //引用bootstrap的样式
import "bootstrap/dist/js/bootstrap.bundle.min.js"; //引用bootstrap的js
import "bootstrap-icons/font/bootstrap-icons.min.css"; //引用图标库的样式
import "swiper/css/swiper.min.css";
import './assets/fonts/font.css'
import Swiper from "swiper";
import router from "./router";
import VueLazyload from 'vue-lazyload'  //引入这个懒加载插件
Vue.prototype.$swiper = Swiper;

//自定义缓存方法挂载全局
import { SetStorage, GetStorage } from "./utils/LocalStorage";
Vue.mixin({
  created() {
    this.$SetStorage  = SetStorage;
    this.$GetStorage = GetStorage;
  }
});
//markdown 编辑器
import VMdPreview from '@kangc/v-md-editor/lib/preview';
import '@kangc/v-md-editor/lib/style/preview.css';
import githubTheme from '@kangc/v-md-editor/lib/theme/github.js';
import '@kangc/v-md-editor/lib/theme/style/github.css';
// highlightjs
import hljs from 'highlight.js';
// 按需引入语言包
import json from 'highlight.js/lib/languages/json';

hljs.registerLanguage('json', json);
VMdPreview.use(githubTheme, {
  Hljs: hljs,
  extend(md) {
    // md为 markdown-it 实例，可以在此处进行修改配置,并使用 plugin 进行语法扩展
    // md.set(option).use(plugin);
  },
});
Vue.use(VMdPreview);


//图片懒加载


Vue.use(VueLazyload, {
  preLoad: 1,
  error: require('@/assets/ProjectImg/error.png'),
  loading: require('@/assets/ProjectImg/loading.png'),
  attempt: 2,
})
Vue.directive('lazy', VueLazyload.directive)

Vue.config.productionTip = false;
new Vue({
  store,
  render: (h) => h(App),
  router
}).$mount("#app");
